.gallery {
  width: 100%;
  background-image: linear-gradient(
    to bottom,
    $background-color-4 40%,
    $white-color 40%,
    $white-color 60%
  );
  &-img {
    width: 100%;
  }
}

.gallery__masonry {
  padding: 0%;
  margin: 0;
}
.sizer1 {
  width: 100%;
  height: 100%;
}
.sizer2 {
  width: 15%;
  height: auto;
}

.sizer3 {
  width: 20%;
}

.gallary__masonry-item img {
  width: 100%;
  height: auto;
  border-radius: 30px;
}
.gallary__masonry-item {
  position: relative;
}

.gallary__link {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.gallary__masonry-wraper {
  position: absolute;
  background-color: black;
  opacity: 0.5;
  width: 100%;
  top: 0;
  bottom: 0;
  display: none;
  border-radius: 30px;
}

.gallary__masonry-wraper:hover .gallary__masonry-wraper:active {
  display: block;
}

.fa {
  color: $white-color;
  position: absolute;
  top: 50%;
  left: 50%;
}
